html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background: #eceeef;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  position: relative;
}

/* Mode Dark */
body.dark-mode {
  background: linear-gradient(to top, rgb(17, 24, 39) 50%, rgb(0, 0, 0) 100%);
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

/** Media Queries **/
h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

/* Header */
.header {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 4rem 0;
}
@media (min-width: 768px) {
  .header {
    background-color: #fff9f9;
    padding-block: 0;
    padding-inline: 6rem;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 20rem;
    margin: 2rem;
  }
}
@media (min-width: 1024px) {
  .header {
    background-color: #fff9f9;
    padding-block: 0;
    padding-inline: 6rem;
    flex-direction: row;
    justify-content: space-between;
    border-radius: 20rem;
    margin: 2rem;
  }
}
.header__logo {
  order: 2;
}
@media (min-width: 768px) {
  .header__logo {
    order: 0;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    order: 0;
  }
}
.header__heading {
  color: #543ece;
  font-family: "Inspiration", cursive;
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    font-size: 4rem;
  }
}

/* Navegacion */
.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  order: 3;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 2rem;
    order: 0;
  }
}
@media (min-width: 1024px) {
  .navegacion {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 4rem;
    order: 0;
  }
}
.navegacion__enlace {
  color: #543ece;
  font-size: 2rem;
  padding: 1rem;
  font-weight: 600;
  margin-bottom: 2rem;
  transition-property: background-color color;
  transition-duration: 0.35s;
}
.navegacion__enlace:hover {
  color: #0ddaa6;
}
@media (min-width: 768px) {
  .navegacion__enlace {
    font-size: 1.2rem;
    margin-bottom: 0;
  }
  .navegacion__enlace:hover {
    background-color: #543ece;
    padding: 1rem;
    border-radius: 0.2rem;
    color: #0ddaa6;
  }
}
@media (min-width: 1024px) {
  .navegacion__enlace {
    font-size: 2rem;
    margin-bottom: 0;
  }
  .navegacion__enlace:hover {
    background-color: #543ece;
    padding: 1.8rem 1rem;
    border-radius: 0.2rem;
    color: #0ddaa6;
  }
}

/* Toggle */
.toggles {
  align-self: self-end;
  margin-right: 1rem;
  order: 1;
}
@media (min-width: 768px) {
  .toggles {
    align-self: center;
    margin-right: 0;
    order: 0;
  }
}
@media (min-width: 1024px) {
  .toggles {
    align-self: center;
    margin-right: 0;
    order: 0;
  }
}

.toggle {
  position: relative;
  top: 0;
  bottom: 0;
  width: 60px;
  height: 25px;
  margin: 0 auto;
  border-radius: 40px;
}
.toggle [type=checkbox] {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  margin: 0px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
}
.toggle span {
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  overflow: hidden;
  opacity: 1;
  background-color: #FFFFFF;
  border: 1px solid #543ece;
  border-radius: 40px;
  transition: 0.2s ease background-color, 0.2s ease opacity;
}
.toggle span::before, .toggle span::after {
  content: "";
  position: absolute;
  top: 8px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transition: 0.5s ease transform, 0.2s ease background-color;
}
.toggle span::before {
  background-color: #FFFFFF;
  transform: translate(-58px, 0px);
  z-index: 1;
}
.toggle span::after {
  background-color: #543ece;
  transform: translate(6px, -6px);
  z-index: 0;
}
.toggle input[type=checkbox]:checked + span {
  background-color: #543ece;
}
.toggle input[type=checkbox]:active + span {
  opacity: 0.5;
}
.toggle input[type=checkbox]:checked + span:before {
  background-color: #543ece;
  transform: translate(28px, -10px);
}
.toggle input[type=checkbox]:checked + span:after {
  background-color: #FFFFFF;
  transform: translate(36px, -5px);
}

/* Home */
@media (min-width: 768px) {
  .home {
    display: grid;
    grid-template-columns: 2fr 1fr;
    height: 50rem;
    align-items: center;
  }
}
.home__heading {
  color: #3b9780;
}
.home__heading a {
  display: block;
  font-size: 5rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  animation: gradient 10s ease-in-out infinite;
  background: linear-gradient(-45deg, #FFFFFF, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 300%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
@keyframes gradient {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
.home__typewriter {
  font-family: "Overpass", sans-serif;
  color: #e73c7e;
  font-size: 2.5rem;
  font-weight: 100;
  font-style: italic;
  opacity: 0.8;
  margin-bottom: 20px;
  letter-spacing: 4px;
  border-right: solid 3px #ffffff;
  white-space: nowrap;
  overflow: hidden;
  animation: animated-text 4s steps(29, end) 1s 1 normal both, animated-cursor 600ms steps(29, end) infinite;
  /* Animation */
  /* Cursor Animation */
}
@keyframes animated-text {
  from {
    width: 0;
  }
  to {
    width: 250px;
  }
}
@keyframes animated-cursor {
  from {
    border-right-color: #3b9780;
  }
  to {
    border-right-color: transparent;
  }
}
.home__texto {
  color: #444;
}
.home__image img {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .home__image img {
    width: 80%;
    margin: 0 auto;
  }
}

/* About */
.about__cabecera {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.about__heading {
  color: #543ece;
  font-weight: 600;
  font-size: 4rem;
  font-size: 3rem;
}
.about__heading::before {
  content: "";
  border: 1px solid #9c8fe3;
  border-radius: 0.2rem;
  padding-right: 0.5rem;
  margin-right: 1rem;
  background-color: #9c8fe3;
}
@media (min-width: 768px) {
  .about__heading {
    font-size: 4rem;
  }
}
.about__boton {
  padding: 0.5rem 1rem;
  border: none;
  background-color: #543ece;
  border-radius: 4px;
  color: #FFFFFF;
  font-weight: 400;
  font-family: var("Poppins", sans-serif);
  transition-property: transform;
  transition-duration: 0.5s;
  padding: 1.5rem 2rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .about__boton {
    width: auto;
  }
}
.about__boton:hover {
  transform: translateY(4px);
}
@media (min-width: 768px) {
  .about__boton {
    padding: 1.5rem 3rem;
  }
}

/* Entries */
@media (min-width: 768px) {
  .entries__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.entrie__contenido {
  background-color: rgba(48, 19, 177, 0.65);
  max-height: 100rem;
  padding: 2rem;
  border-radius: 1rem;
}
.entrie__heading {
  font-size: 2.5rem;
  font-weight: 600;
  display: inline-block;
  margin-top: 5rem;
  color: #7867d8;
}
@media (min-width: 768px) {
  .entrie__heading {
    font-size: 3rem;
    margin-top: 0;
  }
}
.entrie__heading::after {
  content: "";
  background-color: #d9d9d9;
  height: 0.5rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.4rem 0 0 0;
}
.entrie__lista li h3 {
  color: #eceeef;
  font-size: 2.5rem;
}
.entrie__lista li p {
  color: #eceeef;
  font-size: 2rem;
  font-weight: 600;
  margin: 0.5rem 0 2rem 0;
}
.entrie__lista li p span {
  display: block;
  font-weight: 300;
}

/* Projects */
.projects__heading {
  color: #543ece;
  font-weight: 600;
  font-size: 4rem;
  font-size: 3rem;
}
.projects__heading::before {
  content: "";
  border: 1px solid #9c8fe3;
  border-radius: 0.2rem;
  padding-right: 0.5rem;
  margin-right: 1rem;
  background-color: #9c8fe3;
}
@media (min-width: 768px) {
  .projects__heading {
    font-size: 4rem;
  }
}
@media (min-width: 768px) {
  .projects__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .projects__grid {
    display: grid;
    gap: 8rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.project {
  margin-top: 5rem;
}
.project__grid {
  display: flex;
  flex-direction: column-reverse;
  gap: 2rem;
  padding: 2rem 0;
  background-color: rgba(48, 19, 177, 0.65);
  border-radius: 1rem;
  max-height: 70rem;
  transition: transform 0.3s;
}
.project__grid:hover {
  transform: rotate(4deg) scale(1.1);
}
.project__heading {
  color: #FFFFFF;
  text-align: center;
  font-size: 3rem;
  font-weight: 500;
  font-family: "Overpass", sans-serif;
  letter-spacing: 0.5rem;
  text-transform: uppercase;
}
.project__recursos {
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding-bottom: 2rem;
}
.project__imagen {
  width: 5rem;
}
.project__picture img {
  width: 80%;
  margin: 0 auto;
  border-radius: 0.5rem;
}
.project__enlace {
  display: flex;
  justify-content: center;
  margin-block: 1rem;
}
.project__enlace a {
  text-align: center;
  text-transform: uppercase;
  padding: 1rem 4rem;
  border: none;
  background-color: #543ece;
  border-radius: 4px;
  color: #cccccc;
  font-size: 1.2rem;
  font-weight: 600;
  transition-property: transform;
  transition-duration: 0.3s;
  cursor: pointer;
}
.project__enlace a:hover {
  transform: scale(0.9);
}